.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.noteitmcontainer{
  display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
}
.note-icons i{
color: green;
font-size: 30px;
cursor: pointer;
margin-right: 20px;
}
.note-icons{
  display: flex;
      justify-content:start;
      align-items: center;
}

.user-icon{
  color: white;
  font-size: 40px;
  margin-right: 5px;
  cursor: pointer;
}

.user_Container{
  position: fixed;
    top: 12px;
    right:0px;
}

nav{
  position: relative;
}

.user{
  display: flex;
}

.user button{
  margin-right: 5px;
}
input[type="file"]{
  display: none;
}
.img-label{
  cursor: pointer;  
  margin-top: 8px;
}
.img-form{
  object-fit: cover;;
}
.password-container{
  height: 50px;
  background-color: whitesmoke;
  margin-top: 20px;
  line-height: 50px;
  width:58.333333333%;

}
.password-container div {
display: flex;
justify-content: space-between;
}
.password-container:hover{
  background-color: #f1f1f1;
}
.password-container p{
  cursor: pointer;
  font-family: "Roboto", serif;
  font-weight: 700;
  font-style: normal;
}

@media(max-width:991px){
  .user_Container{
      position: absolute;
      top: 12px;
      right: 80px;
  
  }
  .password-container{
    width:75%
  }
  
  }
  @media(max-width:768px){
    .noteitmcontainer{
      justify-content:center;
    }
    .password-container{
      width:100%
    }
    }

    @media(max-width:458px){
      .password-container div{
        display: block;
      }
    
    }